@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat);

.loading{width:100px;height:500px;margin:0 auto;padding:50px 0}.text{color:#fbae17;display:inline-block;margin-left:5px}.bounceball{position:relative;display:inline-block;height:37px;width:15px}.bounceball:before{position:absolute;content:"";display:block;top:0;width:15px;height:15px;border-radius:50%;background-color:#fbae17;-webkit-transform-origin:50%;transform-origin:50%;-webkit-animation:bounce 500ms alternate infinite ease;animation:bounce 500ms alternate infinite ease}@-webkit-keyframes bounce{0%{top:30px;height:5px;border-radius:60px 60px 20px 20px;-webkit-transform:scaleX(2);transform:scaleX(2)}35%{height:15px;border-radius:50%;-webkit-transform:scaleX(1);transform:scaleX(1)}100%{top:0}}@keyframes bounce{0%{top:30px;height:5px;border-radius:60px 60px 20px 20px;-webkit-transform:scaleX(2);transform:scaleX(2)}35%{height:15px;border-radius:50%;-webkit-transform:scaleX(1);transform:scaleX(1)}100%{top:0}}
